import moment from 'moment';
import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, FormGroup, Label, Row } from 'reactstrap';
import { currentDate, DATES, format, InputGroupInline, InputType, isEmpty, WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../../../dependencies/empresaMFPDependency';
import { hasGrupoEmpresaConfig } from '../../../../../../dependencies/grupoEmpresaConfigDependency';
import { getDefaultReportBasicValues } from '../../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../../models/dtos/ger/reportViewOptions';
import { Reducers } from '../../../../../../store/ducks';
import { GlobalState } from '../../../../../../store/ducks/global.duck';
import CustomizacaoTermoIS from '../../../../../components/mfp/customizacaoTermoIS/CustomizacaoTermoIS';
import VinculoIS from '../../../../../components/mfp/vinculoIS/VinculoIS';
import ReportForm from '../../../../../components/report/ReportForm';
import { CLASS_KEY, ReciboFeriasRV, REPORT_EVENT, REPORT_TITLE } from './reciboFeriasRV';

const DEFAULT_VALUES: ReciboFeriasRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
  incluirFeriasPorDtPagamento: true,
  trabalhadores: [],
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT, DATES.value),
  dtFinalLabel: 'Data Final',
  dtFinalRequired: true,
  dtInicialLabel: 'Data Inicial',
  dtInicialRequired: true,
  exibirConfiguracaoDadosEmpresa: false,
};

const ProvisaoFeriasReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { handleSubmit, ...formProps } = useForm<ReciboFeriasRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });

  const [isCheckedAvisoFerias, setisCheckedAvisoFerias] = useState(false);
  const [isHiddenTermo, setIsHiddenTermo] = useState(false);
  const [isHiddenMP, setIsHiddenMP] = useState(false);

  useEffect(
    () => {
      setTimeout(() => {
        if (globalReducer.globalParameter.empresa !== null && globalReducer.globalParameter.empresaMFP !== null) {
          const refStart = moment(globalReducer.globalParameter.empresaMFP.referencia, 'YYYY-MM');
          const refEnd = moment(globalReducer.globalParameter.empresaMFP.referencia, 'YYYY-MM');
          refStart.startOf('month');
          refEnd.endOf('month');

          formProps.setValue('dtInicial', format(refStart.toDate(), 'DD/MM/YYYY'), true);

          formProps.setValue('dtFinal', format(refEnd.toDate(), 'DD/MM/YYYY'), true);

          formProps.setValue('dtAssinatura', format(currentDate(), 'DD/MM/YYYY'), true);
        }
      }, 800);
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const changeIsCheckedAvisoFerias = () => {
    if (isCheckedAvisoFerias) {
      setIsHiddenTermo(false);
      setIsHiddenMP(false);
      setisCheckedAvisoFerias(false);
    } else {
      setisCheckedAvisoFerias(true);
      setIsHiddenTermo(true);
      setIsHiddenMP(true);
    }
  };

  const validateExtras = () => {
    const reciboFeriasRV: ReciboFeriasRV = formProps.getValues();
    const erros = [];

    if (reciboFeriasRV.imprimirAvisoFerias === true) {
      if (isEmpty(reciboFeriasRV.termoAvisoFerias)) {
        erros.push('Termo - Campo não pode estar em branco.');
      }
    }

    if (!isEmpty(reciboFeriasRV.dtInicial) && !isEmpty(reciboFeriasRV.dtFinal)) {
      if (moment(reciboFeriasRV.dtInicial, 'DD/MM/YYYY') > moment(reciboFeriasRV.dtFinal, 'DD/MM/YYYY')) {
        erros.push('A data inicial não pode ser maior ou igual a data final.');
      }
    }

    return erros;
  };

  return (
    <WindowComponent
      title="Recibo de Férias"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[
        () => hasEmpresa(globalReducer.globalParameter),
        () => hasEmpresaMFP(globalReducer.globalParameter),
        () => hasGrupoEmpresaConfig(globalReducer.globalParameter),
      ]}
      wikiId={2686}
      wikiTitle={'Relatório - Recibo de Férias'}
    >
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
        validateExtras={validateExtras}
      >
        <FormGroup tag={Row}>
          <Col>
            <CustomInput
              type="switch"
              label="Incluir férias com data de pagamento dentro do período"
              id="incluirFeriasPorDtPagamento"
              name="incluirFeriasPorDtPagamento"
              innerRef={formProps.register}
            />
          </Col>
        </FormGroup>
        <VinculoIS
          id="trabalhadores"
          formProps={{ ...formProps, validation: { required: true } }}
          label="Trabalhadores(s)"
          placeholder="Selecione Trabalhadore(s)"
          isPrependLabel={true}
          inputSearchProps={{ selectProps: { isMulti: true } }}
        />
        <FormGroup tag={Row}>
          <Col>
            <CustomInput
              type="switch"
              label="Imprimir Aviso de Férias"
              id="imprimirAvisoFerias"
              name="imprimirAvisoFerias"
              innerRef={formProps.register}
              checked={isCheckedAvisoFerias}
              onChange={changeIsCheckedAvisoFerias}
            />
          </Col>
        </FormGroup>
        {isHiddenTermo && (
          <CustomizacaoTermoIS
            formProps={{ ...formProps, validation: { required: true } }}
            label="Termo"
            searchViewProps={{ apenasAvisoFerias: true }}
            apenasAvisoFerias={true}
            placeholder="Selecione o Termo"
            id="termoAvisoFerias"
            isPrependLabel={true}
            inputSearchProps={{ size: 'xl' }}
          />
        )}
        {isHiddenMP && (
          <FormGroup tag={Row}>
            <Col className="custom-radio custom-control custom-control-inline">
              <CustomInput type="switch" id="mp927" name="mp927" innerRef={formProps.register} />
              <Label htmlFor="mp927" style={{ color: 'red' }}>
                {' '}
                Observação com texto de 48 horas, seguindo (MPV-927)
              </Label>
            </Col>
          </FormGroup>
        )}
        <InputGroupInline
          id="dtAssinatura"
          formProps={formProps}
          isPrependLabel={true}
          placeholder="Informe a Data de Assinatura"
          label="Data de Assinatura"
          type={InputType.DATE}
        />
      </ReportForm>
    </WindowComponent>
  );
};

export default ProvisaoFeriasReportView;
